/* styles.css */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f5f5f5;
  }
  
  .app-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  h1 {
    text-align: center;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  table, th, td {
    border: 1px solid #ccc;
  }
  
  th, td {
    padding: 10px;
    text-align: center;
  }
  
  th {
    background-color: #f0f0f0;
  }
  
  .title {
    white-space: nowrap; /* Prevent line breaks */
  }

  .highlighted-row {
    background-color: lightgreen;
  }


  .light-mode th {
    background-color: #f5f5f5; /* Light gray background color */
    color: #333; /* Dark gray text color */
  }
  
  /* Style for table header cells in dark mode */
  .dark-mode th {
    background-color: #333; /* Dark gray background color */
    color: #f5f5f5; /* Light gray text color */
  }
  

  .input-fields-container {
    display: flex;
  }
  
  .input-fields {
    flex: 1 1; /* Take 50% of the width */
    padding-right: 8px; /* Add spacing between columns */
  }
  
  .toggle-button {
    flex: 1 1; /* Take the other 50% of the width */
    display: flex;
    justify-content: flex-end; /* Align the toggle button to the right */
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

