/* styles.css */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f5f5f5;
  }
  
  .app-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  h1 {
    text-align: center;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  table, th, td {
    border: 1px solid #ccc;
  }
  
  th, td {
    padding: 10px;
    text-align: center;
  }
  
  th {
    background-color: #f0f0f0;
  }
  
  .title {
    white-space: nowrap; /* Prevent line breaks */
  }

  .highlighted-row {
    background-color: lightgreen;
  }


  .light-mode th {
    background-color: #f5f5f5; /* Light gray background color */
    color: #333; /* Dark gray text color */
  }
  
  /* Style for table header cells in dark mode */
  .dark-mode th {
    background-color: #333; /* Dark gray background color */
    color: #f5f5f5; /* Light gray text color */
  }
  

  .input-fields-container {
    display: flex;
  }
  
  .input-fields {
    flex: 1; /* Take 50% of the width */
    padding-right: 8px; /* Add spacing between columns */
  }
  
  .toggle-button {
    flex: 1; /* Take the other 50% of the width */
    display: flex;
    justify-content: flex-end; /* Align the toggle button to the right */
  }